.contact{
  width: 100%;
  min-height: 100vh;
  background-color: $black-shadow;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99999;
  text-align: center;
  &_card{
    width: 400px;
    border-radius: 20px;
    padding: 20px 15px;
    background: $green linear-gradient($green-light, $green);
    color: $white-light;
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    justify-items: center;
    align-items: flex-start;
    position: relative;
  }
  &_title{
    margin: 0;
    letter-spacing: 1px;
    font-family: $font-viga;
  }
  &_text{
    margin: 0;
    color: $white;
  }
  &_item_container{
    width: 100%;
    height: fit-content;
    max-height: 320px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "client client";
    gap: 16px;
    justify-items: center;
    align-items: center;
    :first-child{
      grid-area: client;
    }
  }
  &_item{
    width: 100%;
    min-height: 80px;
    height: fit-content;
    border-radius: 18px;
    padding: 6px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.25);
    background-color: $white-light;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: $black-dark;
    text-align: center;
    text-decoration: none;
    position: relative;
    transition: box-shadow .2s;
    &:hover{
      box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.45);
      & > i {
        transform: scale(1.2);
      }
    }
    & > i{
      font-size: 44px;
      transition: transform .2s;
    }
    .fa-phone{
      color: $phone;
      font-size: 34px;
    }
    .fa-envelope{
      color: $email;
      font-size: 34px;
    }
    .fa-instagram{
      background: $instagram;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .fa-facebook{
      color: $facebook;
    }
    &_text{
      margin: 10px 0;
      color: $black-light;
      font-size: .9em;
    }
  }
  &_button-close{
    width: 30px;
    height: 30px;
    border: unset;
    border-radius: 15px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.25);
    background: $red-light;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -15px;
    right: -15px;
    color: $white-light;
    font-size: 20px;
    cursor: pointer;
    z-index: 99999;
    &:hover{
      background: $red;
    }
  }
}

@media (max-width: 425px) {
  .contact{
    &_card{
      width: calc(100% - 32px);
    }
    &_text-empty{
      margin: 1.5rem 10px 100px;
    }
    &_button{
      &-empty{
        font-size: 1.4rem;
      }
    }
  }
}
